@import 'mixins';

/*
Site Footer
---------------------------------------------------------------------------------------------------- */
.site-footer{
  background-color: @darkGray;
  color: @white;
  //.customheader(1.3);
  font-size: 1.3rem;
  text-align: center;
  .mobile-padding;
  padding-bottom: 20px;

  @media @desktop {
    padding: 2rem;
  }

  .genesis-nav-menu .sub-menu {
    display: none;
  }

  a {
    color: @white;
    &:hover{
      color: fade(@white,80%);
    }
  }
  .wrap{
    position: relative;
    .social, .footer-menu{
      display: inline-block;
    } 
    p {
      margin-bottom: 0;
    }
    div.icons{
      display: block;
      position: relative;
      i.icon{
        display: inline-block;
        height: 15px;
        width: 20px;
        margin: 0 4px;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
        font-size: 18px;
        font-weight: 700;
        vertical-align: middle;
      }
      i.icon-equal-housing{
        background-image:url(../img/icon-equal-housing.png);
      }
      i.logo{
        height: 40px;
        position: relative;
        display: inline-block;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
      }
      i.logo-leed{
        width: 40px;
        background-image: url(../img/logo-leed.svg);
      }
      i.pet-friendly{
        background-image: url(../img/pet-friendly.png);
        background-size: contain;
        padding-top: calc(122/100*20px);
        height: auto;
        width: 20px;
      }
      i.logo-lcs{
        background-image: url("../img/lcs_white_logo.png");
        background-position: right center;
        background-size: contain;
        font-style: normal;
        //line-height: 3;
        //max-height: 30px;
        height: auto;
        text-align: left;
        //margin-top: 7px;
        max-width: 290px;
        width: 100%;
        padding-top: 200/2256*290px;
        vertical-align: middle;

        @media  @desktop {
          height: 40px;
          //top: 12px;
          margin: 0 4px;
        }
      }
    }
    .backtotop{
      position: absolute;
      right: 0;
      top: -2px;
      background-color: @white;
      border-radius: 0 0 20% 20%;
      height: 2.4rem;
      width: 2.4rem;
      text-align: center;
      a{
        //color: @orange;
        font-size: 2em;
        line-height: 1;
      }
    }
  }
  .row {
    margin: 0;
  }
}
